<template>
  <section class="card-body p-0">
    <table class="table table-bordered border-primary m-0 overflow-x-scroll">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">PRODUCT</th>
          <th scope="col">Size</th>
          <th scope="col">Price</th>
          <th scope="col">Qty</th>
          <th scope="col">SUB</th>
          <th scope="col">RMK</th>
        </tr>
      </thead>
      <tbody class="table-group-divider luluText">
        <template v-for="(item, index) in cartList.carts" :key="index">
          <tr>
            <td>{{ index + 1 }}</td>
            <td>{{ item.product.title }}</td>
            <template v-for="(data, key) in item.option" :key="key">
              <template v-if="key === 0">
                <td>{{ data.info.size }}</td>
                <td>${{ data.info.price }}</td>
                <td class="text-center">
                  <a
                    href="#"
                    class="me-1 text-decoration-none"
                    @click.prevent="addCart(-1, data, item)"
                    >-</a
                  ><span>{{ data.quantity }}</span
                  ><a
                    href="#"
                    class="ms-1 text-decoration-none"
                    @click.prevent="addCart(1, data, item)"
                    >+</a
                  >
                </td>
                <td>${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
                <td>
                  <span class="text-truncate d-none d-md-inline-block">{{ data.remark }}</span
                  ><i class="bi bi-pencil"></i>
                </td>
              </template>
            </template>
          </tr>
          <template v-for="(data, key) in item.option" :key="key">
            <tr v-if="item.option.length !== 1 && key !== 0">
              <td colspan="2"></td>
              <td>{{ data.info.size }}</td>
              <td>${{ data.info.price }}</td>
              <td class="text-center">
                <a
                  href="#"
                  class="me-1 text-decoration-none"
                  @click.prevent="addCart(-1, data, item)"
                  >-</a
                ><span>{{ data.quantity }}</span
                ><a
                  href="#"
                  class="ms-1 text-decoration-none"
                  @click.prevent="addCart(1, data, item)"
                  >+</a
                >
              </td>
              <td>${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
              <td>
                <span class="text-truncate d-none d-md-inline-block">{{ data.remark }}</span
                ><i class="bi bi-pencil"></i>
              </td>
            </tr>
          </template>
        </template>
        <tr>
          <td colspan="2">
            <div class="d-flex align-items-center dashBottom">
              <i class="bi bi-dot h3 text-danger mb-0"></i>
              <h6>pie</h6>
            </div>
            <div class="d-flex align-items-center dashBottom">
              <i class="bi bi-dot h3 text-info mb-0"></i>
              <h6>cake</h6>
            </div>
            <div class="d-flex align-items-center">
              <i class="bi bi-dot h3 text-secondary mb-0"></i>
              <h6>cookie</h6>
            </div>
          </td>
          <td colspan="3" class="align-middle">total:</td>
          <td colspan="2" class="text-sm-end text-center align-middle">
            <button class="btn btn-outline-primary rounded-0">
              <i class="bi bi-caret-right-fill"></i>CHECKOUT
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </section>
</template>

<style lang="scss" scoped>
.dashBottom {
  margin-bottom: -30px;
}
@media (max-width: 486px) {
  .dashBottom {
    margin-bottom: -15px;
  }
}
</style>

<script>
import emitter from '@/assets/javascript/emitter';

export default {
  data() {
    return {
      cartList: [],
    };
  },
  methods: {
    getCarts() {
      emitter.emit('loadingStatus', true);
      const api = `${process.env.VUE_APP_API}/carts`;
      this.$http
        .get(api)
        .then((res) => {
          this.cartList = res.data.data;
          this.populateCarts(this.cartList.carts);
          emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          emitter.emit('loadingStatus', false);
        });
    },
    populateCarts(data) {
      // 先將選項拉出，比對product裡頭的選項，將正確得填入選項
      data.forEach((item) => {
        item.option.forEach((e) => {
          item.product.options.forEach((index) => {
            // eslint-disable-next-line no-underscore-dangle
            if (index._id === e.size) {
              e.info = index;
            }
          });
        });
      });
    },
    addCart(num, data, item) {
      console.log(data);
      const api = `${process.env.VUE_APP_API}/cart`;
      // 如果等於0執行刪除該品項
      if (data.quantity + num === 0) {
        this.$http
          // eslint-disable-next-line no-underscore-dangle
          .delete(api, { data: { productId: item.product._id, optionId: data._id } })
          .then((res) => {
            console.log(res);
            this.getCarts();
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$http
          .post(api, {
            // eslint-disable-next-line no-underscore-dangle
            productId: item.product._id,
            option: { size: data.size, quantity: num },
          })
          .then((res) => {
            console.log(res);
            this.getCarts();
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
  },
  mounted() {
    this.getCarts();
    emitter.on('getCatrs', () => {
      this.getCarts();
    });
  },
};
</script>
